// @import 'styles/base';
.booking-restaurant-card-container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom: 30px;
  border-radius: 5px;
  overflow: hidden;
  .image_banner {
    width: 100%;
    height: 100%;
    object-fit: cover;
    height: 400px;
  }
  .button-custom {
    margin-top: 30px;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fe5466;
    width: 263px;
    height: 48px;
    text-transform: capitalize;
    font-size: 1.7rem;
    font-weight: 600;
    color: white;
    cursor: pointer;
    font-family: 'Google Sans', sans-serif;
  }
  .avatar {
    width: 52px;
    height: 52px;
    border-radius: 52px;
    background-color: white;
    align-items: center;
    justify-content: center;
  }
  .merchant_image {
    border-radius: 24px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 48px;
    height: 48px;
  }
  .title-section {
    display: flex;
    flex-direction: row;
    align-items: center;
    .restaurant-name {
      margin-left: 33px;
      font-weight: 700;
      font-family: 'Google Sans', sans-serif;
      font-size: 2.2rem;
    }
  }
  .star-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  .info-restaurant-section {
    margin-left: 18px;
    margin-right: 68px;
    margin-top: 18px;
    margin-bottom: 20px;
  }
  .location-section {
    .location-title {
      font-size: 1.6rem;
      font-weight: 600;
      font-family: 'Google Sans', sans-serif;
    }
    .location-detail {
      height: 70px;
      margin-top: 5px;
      font-weight: 400;
      font-family: 'Google Sans', sans-serif;
      font-size: 1.6rem;
    }
  }
  .other-branch-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 40px;
    .other-branch-text {
      font-weight: 600;
      opacity: 0.5;
      font-family: 'Google Sans', sans-serif;
      font-size: 1.6rem;
    }
    .see-all-text {
      background-color: white;
      border: none;
      .see-all {
        font-weight: bold;
        font-size: 1.6rem;
        color: #c4a732;
      }
    }
    .see-all-text:focus {
      background-color: white;
      border: none;
      outline: none;
    }
    .see-all-text:hover {
      background-color: #e5e5e5;
    }
  }
  .line-section {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
    .line {
      margin-top: 21px;
      width: 100%;
      border: 0.2px solid #000000;
    }
  }
  .footer-section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 21px;
  }
  @media only screen and (max-width: 900px) {
    .info-restaurant-section {
      margin: 20px 20px;
    }
    .star-section {
      margin-top: 15px;
      margin-bottom: 15px;
      justify-content: flex-start;
    }
    .title_container {
      flex-direction: column;
      align-items: flex-start;
    }
    .image_banner {
      height: 200px;
    }
  }
}
